.home{
    display:flex;
    flex-direction:column;
    height:100vh;
}

.home__header{
    display:flex;
    justify-content: space-between;
    padding: 1px 30px;
    align-items: center;
    
}

.home__header a{
    margin-right: 20px;
    text-decoration: inherit;
    color:rgba(98, 94, 94, 0.87);
    font-weight: 400;
    font-size: 15px;
}

.home__header a:hover{
    text-decoration: underline;

}
.home__headerRight{
    display:flex;
    align-items: center;
    min-width: 13vw;
    justify-content: space-between;
}
.home__headerRight > .MuiSvgIcon-root{
    margin: 20px;
}
.home__body{
    flex:1;
    display:flex;
    margin-top: 8%;
    flex-direction: column;
}
.home__body > img{
  object-fit: contain;
  height: 100px;
}
.home__footer{
    display:flex;
    flex:1;
    align-items: center;
    flex-direction: column;
    margin-top: 10%;
    margin-bottom: 1%;
    /* margin-left: ; */

}